import { Component, Event, EventEmitter, Prop, h } from '@stencil/core';

/**
 * @slot label - For the chip label
 * @slot icon - For the chip Icon
 */
@Component({
  tag: 'mdx-chip',
  styleUrl: 'chip.scss',
  shadow: true,
})
export class Chip {
  @Event()
  toggle: EventEmitter<{ selected: boolean }>;

  @Prop({ reflect: true })
  selected = false;

  @Prop({ reflect: true })
  disabled = false;

  @Prop()
  ariaLabel?: string = '';

  render() {
    return (
      <button
        part="chip"
        class="chip"
        aria-label={this.ariaLabel}
        onClick={(e) => {
          e.preventDefault();
          if (!this.disabled) {
            this.selected = !this.selected;
            this.toggle.emit({ selected: this.selected });
          }
        }}
      >
        <slot name="icon"></slot>
        <slot name="label"></slot>
        {this.selected && (
          <svg class="chip__icon" width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path
              d="M12 4L4 12M4 4L12 12"
              stroke="currentColor"
              stroke-width="1.5"
              stroke-linecap="round"
              stroke-linejoin="round"
            />
          </svg>
        )}
      </button>
    );
  }
}
