:host .chip {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border-style: solid;
    box-sizing: border-box;
    transition: color linear 0.2s;
    transition: background-color linear 0.2s;
    cursor: pointer;

    padding: var(--mdx-comp-chips-unselected-padding-vertical) var(--mdx-comp-chips-unselected-padding-horizontal);
    gap: var(--mdx-comp-chips-unselected-sizing-gap);
    height: var(--mdx-comp-chips-unselected-sizing-height);
    width: var(--mdx-comp-chips-unselected-sizing-width);
    border-radius: var(--mdx-comp-chips-unselected-border-radius-default);
    border-width: var(--mdx-comp-chips-unselected-border-default);
    border-color: var(--mdx-comp-chips-unselected-border-color-default);
    font: var(--mdx-comp-chips-unselected-font-default);
    background-color: var(--mdx-comp-chips-unselected-background-color-default);
    color: var(--mdx-comp-chips-unselected-color-default);

    & ::slotted([slot="icon"]) {
        color: var(--mdx-comp-chips-unselected-icon-leading-color-default);
    }

    &__icon {
        color: var(--mdx-comp-chips-unselected-icon-close-color-default);
    }
}

:host .chip:hover {
    border-color: var(--mdx-comp-chips-unselected-border-color-hover);
    font: var(--mdx-comp-chips-unselected-font-hover);
    background-color: var(--mdx-comp-chips-unselected-background-color-hover);
    color: var(--mdx-comp-chips-unselected-color-hover);

    & ::slotted([slot="icon"]) {
        color: var(--mdx-comp-chips-unselected-icon-leading-color-hover);
    }

    &__icon {
        color: var(--mdx-comp-chips-unselected-icon-close-color-hover);
    }
}

:host([disabled]) .chip {
    cursor: not-allowed;

    border-color: var(--mdx-comp-chips-unselected-border-color-disabled);
    font: var(--mdx-comp-chips-unselected-font-disabled);
    background-color: var(--mdx-comp-chips-unselected-background-color-disabled);
    color: var(--mdx-comp-chips-unselected-color-disabled);

    & ::slotted([slot="icon"]) {
        color: var(--mdx-comp-chips-unselected-icon-leading-color-disabled);
    }

    &__icon {
        color: var(--mdx-comp-chips-unselected-icon-close-color-disabled);
    }
}

:host([selected]) .chip {
    padding: var(--mdx-comp-chips-selected-padding-vertical) var(--mdx-comp-chips-selected-padding-horizontal);
    gap: var(--mdx-comp-chips-selected-sizing-gap);
    height: var(--mdx-comp-chips-selected-sizing-height);
    width: var(--mdx-comp-chips-selected-sizing-width);
    border-radius: var(--mdx-comp-chips-selected-border-radius-default);
    border-width: var(--mdx-comp-chips-selected-border-default);
    border-color: var(--mdx-comp-chips-selected-border-color-default);
    font: var(--mdx-comp-chips-selected-font-default);
    background-color: var(--mdx-comp-chips-selected-background-color-default);
    color: var(--mdx-comp-chips-selected-color-default);

    & ::slotted([slot="icon"]) {
        color: var(--mdx-comp-chips-selected-icon-leading-color-default);
    }

    &__icon {
        color: var(--mdx-comp-chips-selected-icon-close-color-default);
    }
}

:host([selected]) .chip:hover {
    border-color: var(--mdx-comp-chips-selected-border-color-hover);
    font: var(--mdx-comp-chips-selected-font-hover);
    background-color: var(--mdx-comp-chips-selected-background-color-hover);
    color: var(--mdx-comp-chips-selected-color-hover);

    & ::slotted([slot="icon"]) {
        color: var(--mdx-comp-chips-selected-icon-leading-color-hover);
    }

    &__icon {
        color: var(--mdx-comp-chips-selected-icon-close-color-hover);
    }
}

:host([selected][disabled]) .chip {
    border-color: var(--mdx-comp-chips-selected-border-color-disabled);
    font: var(--mdx-comp-chips-selected-font-disabled);
    background-color: var(--mdx-comp-chips-selected-background-color-disabled);
    color: var(--mdx-comp-chips-selected-color-disabled);

    & ::slotted([slot="icon"]) {
        color: var(--mdx-comp-chips-selected-icon-leading-color-disabled);
    }

    &__icon {
        color: var(--mdx-comp-chips-selected-icon-close-color-disabled);
    }

}